.logo {
    background-image: url('../../images/logo/logo.png');
    width: 50px;
    height: 50px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    align-self: flex-start;
}

a {
    color: rgb(87, 111, 114);
    text-decoration: none;
}

a:hover {
    color: rgb(87, 111, 114);
    text-decoration: none;
}

.admin__nav-holder {
    width: 100%;
    background-color: rgb(230, 224, 187);
    position: static;
    height: 50px;
    text-align: center;
}

.admin__navigation {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    
}

.admin__navigation__link {
    text-decoration: none;
    background-color: rgb(228, 220, 207);
    height: 100%;
    width: 15%;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid rgb(125, 157, 156);
}

.admin__navigation__link:hover {
    background-color: rgb(125, 157, 156);
    text-decoration: none;
}

.btn {
    width: 130px;
    height: 40px;
    border-radius: 5px;
    padding: 10px 25px;
    background: transparent;
    cursor: pointer;
    transition: all 0.3s ease;
    position: relative;
    display: inline-block;
    box-shadow: inset 2px 2px 2px 0px rgba(255, 255, 255, .5),
        7px 7px 20px 0px rgba(0, 0, 0, .1),
        4px 4px 5px 0px rgba(0, 0, 0, .1);
    outline: none;
    background: rgb(125, 157, 156);
    background: linear-gradient(0deg, rgb(125, 157, 156) 0%, rgb(107, 154, 152) 100%);
    border: none;
    font-family: 'Montserrat', sans-serif !important;
    color: rgb(228, 220, 207);

}

.btn:before {
    height: 0%;
    width: 2px;
}

.btn:hover {
    box-shadow: 4px 4px 6px 0 rgba(255, 255, 255, .5),
        -4px -4px 6px 0 rgba(116, 125, 136, .5),
        inset -4px -4px 6px 0 rgba(255, 255, 255, .2),
        inset 4px 4px 6px 0 rgba(0, 0, 0, .4);
}

.btn-container {
    width: 100vw;
    display: flex;
    justify-content: center;
    margin-top: 20px;
    font-family: 'Montserrat', sans-serif !important;

}

button {
    color: rgb(228, 220, 207);
    font-family: 'Montserrat', sans-serif !important;
    margin: 5px;
}